

import React, { useState } from 'react';
import { BrowserRouter, Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import TodoApp from './TodoApp'; 
import Login from './pages/Login.jsx';
import Register from './pages/Register.jsx';
import service from './service.js';

//      (   -State)
const checkAuth = () => {
  return localStorage.getItem('jwtToken') !== null;
};

// **************************  Header  **************************
//       
//  handleLogout -prop
const Header = ({ handleLogout }) => {
    const navigate = useNavigate(); 
    const username = localStorage.getItem('username'); 
    
    const onLogout = () => {
        handleLogout(); //   -State  localStorage
        navigate('/login'); //    
    };

    return (
        <div style={{ padding: '10px 20px', backgroundColor: '#f5f5f5', borderBottom: '1px solid #ddd', display: 'flex', justifyContent: 'space-between' }}>
            <span>Welcome, {username}</span>
            <button onClick={onLogout} style={{ background: '#f44336', color: 'white', border: 'none', padding: '5px 10px', cursor: 'pointer' }}>
                Logout
            </button>
        </div>
    );
};

const ProtectedRoute = ({ isAuthenticated, children }) => {
  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }
  return children;
};


function App() {
  //     State
  const [isAuthenticated, setIsAuthenticated] = useState(checkAuth()); 

  //   -Login.jsx   
  const handleLoginSuccess = () => {
    setIsAuthenticated(true); //   -Header  !
  };

  //   
  const handleLogout = () => {
    service.logout(); //    localStorage (   service.js )
    setIsAuthenticated(false); //   -Header  !
  };


  return (
    <BrowserRouter>
      {/*      -State  */}
      {isAuthenticated && <Header handleLogout={handleLogout} />}
      
      <Routes>
        
        {/*   */}
        <Route path="/register" element={<Register />} />
        {/*   onLoginSuccess  Login */}
        <Route 
            path="/login" 
            element={isAuthenticated ? <Navigate to="/" replace /> : <Login onLoginSuccess={handleLoginSuccess} />} />
        
        {/*    -  -State */}
        <Route 
          path="/" 
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated}>
              <TodoApp /> 
            </ProtectedRoute>
          } 
        />

        {/* 404 */}
        <Route path="*" element={<h1>404 Not Found</h1>} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;