
import axios from 'axios';

//     
const apiBaseUrl = 'http://localhost:5233'; 

// 1.   axios  
const api = axios.create({
    baseURL: apiBaseUrl 
});

// 2. Interceptor  (Request) -   
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('jwtToken');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// 3. Interceptor  (Response) -   401
api.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    if (error.response) {
      console.error("API Response Error:", error.response.data);
      
      //     401 (Unauthorized)
      if (error.response.status === 401) {
          localStorage.removeItem('jwtToken'); 
          localStorage.removeItem('username'); 
          
          //           
          if (window.location.pathname !== '/login' && window.location.pathname !== '/register') {
              window.location.href = '/login'; 
          }
      }
    }
    return Promise.reject(error);
  }
);


export default {
  // ******************************
  //   (Auth) -    (/login, /register)
  // ******************************
  login: async (username, password) => {
  const result = await api.post('/auth/login', { username, password });
    
    //   -JWT   
    localStorage.setItem('jwtToken', result.data.token); 
    localStorage.setItem('username', result.data.username); 
    
    return result.data; 
  },

  register: async (username, password) => {
  const result = await api.post('/auth/register', { username, password });
    return result.data;
  },

  logout: () => {
    localStorage.removeItem('jwtToken');
    localStorage.removeItem('username');
  },

  // ******************************
  //    -    (/items)
  // ******************************
  getTasks: async () => {
    const result = await api.get('/items'); 
    return result.data;
  },

  addTask: async (name) => {
    const task = { name: name, isComplete: false };
    const result = await api.post('/items', task);
    return result.data;
  },

  setCompleted: async (id, isComplete) => {
    const updatedTask = { id: id, isComplete: isComplete };
    //  PUT  , : http://localhost:5233/items/1
    await api.put(`/items/${id}`, updatedTask); 
    return {};
  },

  deleteTask: async (id) => {
    //  DELETE  , : http://localhost:5233/items/1
    await api.delete(`/items/${id}`);
    return {};
  }
};